.index {
  display: flex;
  flex-direction: column;
  padding: 40px;
}
.index input::-webkit-outer-spin-button,
.index input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.index input[type="number"] {
  -moz-appearance: textfield;
}
.index .logo {
  margin-bottom: 40px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.index .logo .left {
  max-width: 200px;
}
.index .logo .right {
  display: flex;
  justify-content: flex-end;
}
.index .logo .right .media {
  width: 8%;
  margin-left: 60px;
  max-width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.index .logo .right .media a {
  color: #fff;
}
.index .logo .right .media img {
  margin-bottom: 10px;
}
.index .logo .title {
  margin-top: 10px;
}
.index .main {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.index .main .desc {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;
  width: 50%;
}
.index .main .desc .slogan {
  font-weight: 600;
  font-size: 24px;
  width: 90%;
}
.index .main .desc .slogan .vs-img {
  max-width: 600px;
  margin-top: 30px;
}
.index .main .desc .profession-img {
  margin: 30px 0px;
  width: 70%;
}
.index .main .desc .para {
  line-height: 2;
}
.index .main .desc .para .serial {
  font-weight: 600;
  margin-right: 12px;
}
.index .main .desc .brief-img {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-content: flex-start;
}
.index .main .role-card {
  margin-left: 30px;
  width: 45%;
  padding: 40px;
  background: #231d39;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.index .main .role-card .set-attr-area {
  display: flex;
  font-size: 24px;
  width: 100%;
  justify-content: space-between;
}
.index .main .role-card .set-attr-area .radio-attr {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.index .main .role-card .set-attr-area .radio-attr .attr {
  text-align: left;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}
.index .main .role-card .set-attr-area .radio-attr .attr .attr-value {
  font-weight: 600;
}
.index .main .role-card .set-attr-area .radio-attr .attr .option-area {
  margin-top: 10px;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  flex: 1 1 auto;
  margin: 4px 0 0 -8px;
}
.index .main .role-card .set-attr-area .radio-attr .attr .option-area .option {
  margin: 8px 0 0 8px;
  font-size: 20px;
  font-weight: 400;
}
.index .main .role-card .input-attr {
  margin-top: 30px;
  margin-left: 40px;
  text-align: left;
}
.index .main .role-card .input-attr .attr-point-value {
  font-weight: 600;
}
.index .main .role-card .input-attr .input-list {
  margin-top: 30px;
}
.index .main .role-card .input-attr .input-list .point-input {
  margin-top: 10px;
  font-weight: 600;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.index .main .role-card .input-attr .input-list .point-input .attr-text {
  text-transform: capitalize;
  font-size: 20px;
  font-weight: 400;
}
.index .main .role-card .input-attr .input-list .point-input .input-box {
  width: 60%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.index .main .role-card .input-attr .input-list .point-input .input-box .attr-input {
  width: 20%;
  text-align: center;
}
.index .main .role-card .input-attr .button-set {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  width: 100%;
}
.index .main .role-card .input-attr .button-set .card-button {
  margin: 10px 0px;
  height: 30px;
}
.index .footer {
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.index .footer .footer-logo {
  width: 10%;
  max-width: 100px;
}
.index .footer .infos {
  margin-top: 20px;
}
